.modal{
    width: 100vw;
    height: 100vh;
}

.modal_shown::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: var(--black);
    opacity: 0.6;
    z-index: 3;
    display: block;
}

.modal_closed{
    z-index: -1;
    display: none;
}

.modal_closed>.modal__content{
    display: none;
}

@keyframes anim {
    0%{
        opacity: 0.5;
    }
    100%{
        opacity: 1;
    }
}

.modal_shown>.modal__content{
    display: flex;
    animation-name: anim;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    opacity: 0;
}

.modal__content{
    height: fit-content;
    padding: 5rem 0;
    z-index: 4;
    position: relative;
}